<template>
  <div class="nav-menu"
    ref="navMenu" 
    :class="menuActiveClass">
    <div class="nav-menu-head">
      <div class="nav-menu-head__logo">
        <img src="../assets/images/svg/big-logo.svg" alt="" height="41">
      </div>
      <button 
        class="nav-menu-close" 
        @click="closeMenu"></button>
    </div>

    <div class="nav-menu-links">
      <router-link 
        to="/News" 
        @click.native="closeMenu">
        <img src="../assets/images/nav-menu/event-icon.svg" alt="">События и акции</router-link>

      <router-link 
        to="/MenuCtegories" 
        @click.native="closeMenu">
        <img src="../assets/images/nav-menu/menu-list-icon.svg" alt="">Меню</router-link>

        <router-link 
          to="/Booking" 
          @click.native="closeMenu"><img src="../assets/images/nav-menu/booking-icon.svg" alt="">Бронь</router-link>

        <router-link 
          to="/Contacts" 
          @click.native="closeMenu"><img src="../assets/images/nav-menu/marker-icon.svg" alt="">Контакты</router-link>

      <a href="#"><img src="../assets/images/nav-menu/avatar-icon.svg" alt="">Личный кабинет</a>
    </div>
  </div>
</template>



<script>
export default {
  methods: {
    closeMenu(e) {
      e.preventDefault();
      this.$store.state.navMenuActive = false;
    }
  },
  
  computed: {
    menuActiveClass() {
      return { active: this.$store.state.navMenuActive }
    }
  },

  mounted() {
    var hammertime = new Hammer(this.$refs.navMenu);
    hammertime.on('swipeleft', ev => {
      this.$store.state.navMenuActive = false;
    });
  },
}
</script>



